  border: 1px solid #e6ebf5;
  border-radius: 4px;
  -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  width: 95px;
}
.chartMenuList li {
  line-height: 16px;
  padding: 0 15px;
  margin: 6px 0;
  font-size: 12px;
  color: #606266;
  cursor: pointer;
  outline: none;
  display: block;
}
.chartMenuList li:hover {
  background-color: #ecf5ff;
  color: #66b1ff;
}

.chartNodataBox {
  width: 100%;
  height: 100%;
}
.chartNodataInner {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -50px;
  margin-left: -32px;
}

.addPanelBox {
  width: 100%;
  height: 100%;
  position: relative;
}
.addPanelBox a {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -54px;
  margin-left: -90px;
  padding: 20px;
  text-align: center;
  border: 2px solid #ccc;
  border-radius: 10px;
}
.addPanelBox .addImg {
  font-size: 48px;
}
.addPanelBox .addTxt {
  font-size: 16px;
}

/* loading loading */
.cgl-wrapper .el-loading-spinner{
  padding: 3px;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  text-align: right;
  margin-top: 0;
}
.cgl-wrapper .el-loading-spinner i{
  color: #242222;
  font-size: 18px;
}
</style>



